<script>
  import { Col, Container, Row, Button } from 'sveltestrap';
  // let query ="";
  let result;
  const api = {
    key: process.env.QUIZ_API_KEY,
    baseURL: 'https://quizapi.io/api/v1/',
  };

  async function getResult() {
    try {
      let response = await fetch(
        `${api.baseURL}questions?apiKey=${api.key}&category=code&difficulty=Medium&limit=1&tags=HTML`
        // { mode: 'no-cors' }
      );
      let text = await response.text();
      let data = text;
      console.log(data);
      return data;
    } catch (err) {
      alert(err);
    }
  }
  function handleClick(e) {
    result = getResult();
  }
</script>

<style>
  main {
    text-align: center;
    padding: 1em;
    max-width: 240px;
    margin: 0 auto;
  }

  h1 {
    color: #ff3e00;
    text-transform: uppercase;
    font-size: 4em;
    font-weight: 100;
  }

  @media (min-width: 640px) {
    main {
      max-width: none;
    }
  }
</style>

<main>
  <Container>
    <Row>
      <Col>
        <Button on:click={handleClick}>Show Questions</Button>
      </Col>
    </Row>
  </Container>
</main>
